/* ------------------------------------------------
    Grid Column Classes
-------------------------------------------------*/
.grid-cols {
  display: grid;
}

.grid-cols--1 {
  grid-template-columns: repeat(1, minmax(0, 1fr));
}

.grid-cols--2 {
  gap: 1em;
  grid-template-columns: repeat(2, minmax(0, 1fr));
}

.grid-cols--3 {
  gap: 1em;
  grid-template-columns: 1fr 1fr 1fr;
}

.grid-cols--4 {
  gap: 1em;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.grid-cols--5 {
  gap: 1em;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}

.grid-cols--3-1 {
  gap: 1em;
  grid-template-columns: 3fr 1fr;
}

.grid-cols--1-3 {
  gap: 1em;
  grid-template-columns: 1fr 3fr;
}

.grid-cols--6-4 {
  gap: 1em;
  grid-template-columns: 6fr 4fr;
}

.grid-cols--4-6 {
  gap: 1em;
  grid-template-columns: 4fr 6fr;
}

.grid-cols--6-3 {
  gap: 1em;
  grid-template-columns: 2fr 0.985fr;
}

.grid-cols--3-6 {
  gap: 1em;
  grid-template-columns: 0.985fr 2fr;
}

@media only screen and (max-width: 1166px) {
  .grid-cols--3,
  .grid-cols--5 {
    grid-template-columns: 1fr 1fr 1fr;
  }
  .grid-cols--4 .grid-cols--6-3,
  .grid-cols--3-6,
  .grid-cols--6-4,
  .grid-cols--4-6,
  .grid-cols--3-1,
  .grid-cols--1-3 {
    grid-template-columns: 1fr 1fr;
  }
}
@media only screen and (max-width: 767px) {
  .grid-cols--2,
  .grid-cols--3,
  .grid-cols--4,
  .grid-cols--6-3,
  .grid-cols--3-6,
  .grid-cols--6-4,
  .grid-cols--4-6,
  .grid-cols--3-1,
  .grid-cols--1-3 {
    grid-template-columns: 1fr;
  }
  .grid-cols--5 {
    grid-template-columns: 1fr 1fr;
  }
}
@media only screen and (max-width: 568px) {
  .grid-cols--5 {
    grid-template-columns: 1fr;
  }
}
