    align-items: stretch;
    padding: 0;
  }
  table.f11y--table td:nth-child(even) > div {
    background-color: rgba(226, 219, 215, 0.035);
  }
  table.f11y--table td:not(:last-child)::before {
    border-bottom: 1px solid rgba(22, 29, 39, 0.15);
