  opacity: 50%;
}
.component--title h2 {
  line-height: 100%;
}

/* Helpers */
.f11y--tabs--code [role=tablist] {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: flex-end;
  gap: 10px;
  padding-bottom: 0.5rem;
  position: relative;
}
.f11y--tabs--code [role=tablist] [role=tab] {
  background-color: #E2DBD7;
  color: #161D27;
  font-weight: 700;
  padding: 7px 10px;
  border-radius: 2px;
  position: relative;
}
.f11y--tabs--code [role=tablist] [role=tab][aria-selected=true] {
  background-color: #161D27;
  color: #E2DBD7;
}
.f11y--tabs--code [role=tablist] [role=tab][aria-selected=true]::after {
  content: "";
  position: absolute;
  transform: translateX(-50%);
  bottom: -8px;
  left: 50%;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 8px solid #161D27;
}

/* Helpers */
.block-toggle {
  user-select: none;
  padding: 3px 3px 3px 0px;
}
.block-toggle input[role=switch] ~ .block-toggle__switch .block-toggle__switch-container {
  position: relative;
  display: inline-block;
  width: 2em;
  height: 1em;
  box-shadow: 0px 0px 0px 2px #161D27;
  top: 3px;
}
.block-toggle input[role=switch] ~ .block-toggle__switch .block-toggle__switch-container .block-toggle__switch-indicator {
  display: inline-block;
  width: 0.8em;
  height: 0.8em;
  background-color: #161D27;
  top: 0.1em;
  left: 0.1em;
  position: absolute;
}
.block-toggle input[role=switch]:not(:checked) ~ .block-toggle__switch .block-toggle__switch-label--active {
  display: none;
}
.block-toggle input[role=switch]:checked ~ .block-toggle__switch .block-toggle__switch-label--disabled {
  display: none;
}
.block-toggle input[role=switch]:checked ~ .block-toggle__switch .block-toggle__switch-container .block-toggle__switch-indicator {
  transform: translateX(calc(100% + 0.2rem));
}
.block-toggle:hover {
  cursor: pointer;
}
.block-toggle input[role=switch]:focus-visible ~ .block-toggle__switch .block-toggle__switch-container {
  outline: 2px solid #161D27;
  outline-offset: 3px;
}

/* Helpers */
.block-tabs .block-tabs__tab[aria-selected=true] {
  background-color: #161D27;
}
.block-tabs .block-tabs__tab[aria-selected=true] span {
  color: #E2DBD7;
}

/* Helpers */
.f11y--accordion {
  width: 100%;
  display: flex;
  gap: 1rem;
  flex-direction: column;
}
.f11y--accordion .accordion-item__panel[hidden] {
  display: none;
}
.f11y--accordion [f11y-accordion-item] {
  border-radius: 2px;
}
.f11y--accordion [f11y-accordion-item] button {
  border: 1px solid rgba(226, 219, 215, 0.45);
  padding: 15px 20px;
  width: 100%;
  background-color: transparent;
  font-weight: 500;
  font-style: italic !important;
  font-size: var(--l-body);
  color: #E2DBD7;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  transition: background-color 0.3s cubic-bezier(0.4, 0, 0.6, 1);
}
.f11y--accordion [f11y-accordion-item] button::after {
  content: "";
  width: 17px;
  height: 17px;
  border: 1px solid rgba(226, 219, 215, 0.45);
  border-radius: 100vw;
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.6, 1), background-color 0.3s cubic-bezier(0.4, 0, 0.6, 1);
}
.f11y--accordion [f11y-accordion-item] button[aria-expanded=true] {
  border: 1px solid rgba(226, 219, 215, 0.9);
  border-bottom: 1px solid transparent;
  border-radius: 2px;
  background-color: rgba(226, 219, 215, 0.075);
}
.f11y--accordion [f11y-accordion-item] button[aria-expanded=true]::after {
  background-color: #E2DBD7;
  transform: scale(1.2);
}
.f11y--accordion [f11y-accordion-item] div {
  width: 100%;
}
.f11y--accordion [f11y-accordion-item] [role=region] {
  background-color: #161D27;
  padding: var(--n-heading);
  border-radius: 0px 0px 2px 2px;
  border: 1px solid rgba(226, 219, 215, 0.9);
  border-top: none;
}
.f11y--accordion [f11y-accordion-item] [role=region] * {
  color: #E2DBD7;
  font-size: var(--h-body);
  font-weight: 100;
  font-style: italic;
}
.f11y--accordion .external-triggers {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 10px;
}
.f11y--accordion .external-triggers button.is-closed::before {
  content: "Open ";
}
.f11y--accordion .external-triggers button.is-open::before {
  content: "Close ";
}

/* Helpers */
.f11y--layer {
  display: none;
  position: fixed;
  inset: 0;
  justify-content: center;
  align-items: center;
  padding: 3rem;
  background: rgba(226, 219, 215, 0.3);
  backdrop-filter: blur(2px);
  /* ------------------------------------------------
      Animation Modifier Classes
  -------------------------------------------------*/
}
.f11y--layer.is-open {
  display: flex;
  z-index: 100;
}
.f11y--layer [role=dialog] {
  border-radius: 0.3em;
  background-color: #161D27;
  width: 100%;
  max-width: 780px;
  max-height: 100vh;
  box-sizing: border-box;
  padding: 1.5rem;
  position: relative;
  z-index: 10;
}
.f11y--layer [role=dialog] .x-close {
  width: 40px;
  height: 40px;
  position: absolute;
  right: -15px;
  top: -15px;
  border-radius: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  transform: rotate(45deg);
}
.f11y--layer [role=dialog] .x-close::before, .f11y--layer [role=dialog] .x-close::after {
  content: "";
  position: absolute;
  width: 17px;
  height: 3px;
  border-radius: 3px;
  background-color: #161D27;
  transform-origin: center;
}
.f11y--layer [role=dialog] .x-close::before {
  transform: rotate(-90deg);
}
.f11y--layer.block-modal--animate .block-modal__container, .f11y--layer.block-modal--animate .block-modal__overlay {
  will-change: transform;
}
.f11y--layer.is-animating.is-opening [role=dialog] {
  animation: slide-in-up 0.4s cubic-bezier(0.4, 0, 0.6, 1);
}
.f11y--layer.is-animating.is-closing [role=dialog] {
  animation: slide-out-down 0.4s cubic-bezier(0.4, 0, 0.6, 1);
}
.f11y--layer.is-animating.is-opening {
  animation: fade-in 0.4s cubic-bezier(0.4, 0, 0.6, 1);
}
.f11y--layer.is-animating.is-closing {
  animation: fade-out 0.4s cubic-bezier(0.4, 0, 0.6, 1);
}

/* ------------------------------------------------
    Animation Keyframes
-------------------------------------------------*/
@keyframes fade-in {
  from {
    background: rgba(226, 219, 215, 0);
    backdrop-filter: blur(0px);
  }
  to {
    background: rgba(226, 219, 215, 0.3);
    backdrop-filter: blur(3px);
  }
}
@keyframes fade-out {
  from {
    background: rgba(226, 219, 215, 0.3);
    backdrop-filter: blur(3px);
  }
  to {
    background: rgba(226, 219, 215, 0);
    backdrop-filter: blur(0px);
  }
}
@keyframes slide-in-up {
  from {
    opacity: 0;
    transform: translateY(15%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slide-out-down {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(-10%);
  }
}
/* Helpers */
.block-dropdown {
  /* ------------------------------------------------
      Animation Modifier Classes
  -------------------------------------------------*/
}
.block-dropdown .block-dropdown__menu {
  position: absolute;
  background-color: #E2DBD7;
  border: 1px solid #161D27;
  transform-origin: top center;
}
.block-dropdown .block-dropdown__menu[aria-hidden=true] {
  display: none;
}
.block-dropdown .block-dropdown__menu[aria-hidden=false] {
  display: block;
}
.block-dropdown [role=menuitem],
.block-dropdown [role=separator] {
  display: block;
}
.block-dropdown .block-dropdown__toggle svg.down {
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.6, 1);
}
.block-dropdown .block-dropdown__toggle[aria-expanded=true] svg.down {
  transform: rotateX(180deg);
}
.block-dropdown.block-dropdown--animate .block-dropdown__menu {
  animation: block-dropdown--animate-out 0.3s cubic-bezier(0.4, 0, 0.6, 1);
}
.block-dropdown.block-dropdown--animate.is-open .block-dropdown__menu {
  animation: block-dropdown--animate-in 0.3s cubic-bezier(0.4, 0, 0.6, 1);
}

/* ------------------------------------------------
    Animation Keyframes
-------------------------------------------------*/
@keyframes block-dropdown--animate-in {
  from {
    opacity: 0;
    transform: scaleY(0.5);
  }
  to {
    opacity: 1;
    transform: scaleY(1);
  }
}
@keyframes block-dropdown--animate-out {
  from {
    opacity: 1;
    transform: scaleY(1);
  }
  to {
    opacity: 0;
    transform: scaleY(0.5);
  }
}
